<template>

  <div>
    <ul><center>
        <li v-for="(qnaItem) in qnalist" v-bind:key="qnaItem.num">
          <i class="checkBtn fab fa-quora"></i>
          <span @click="getQna(qnaItem.num)">{{qnaItem.title}}</span>
          <span class="removeBtn" type="button" @click="removeQna(qnaItem.num)">
            <i class="far fa-trash-alt"></i>
          </span>
      </li></center>
    </ul>
  </div>
</template>

<script>
import Constant from '../Constant';

export default {
  created () {
    this.$store.dispatch(Constant.GET_QNALIST); //action call
  },
  computed: {
    //store안의 qnalist check
    qnalist() {
      return this.$store.state.qnalist; 
    }
  },
  methods: {
    removeQna : function(num) {
        this.$store.dispatch(Constant.REMOVE_QNA, {num}); 
    },
    getQna: function(num){
      this.$router.push('/detail/'+num); 
    }
  },
};
</script>

